import * as React from 'react';
import { useState } from 'react';
import { PersonalSummary } from './personal-summary/personal-summary.component';
import { EmploymentHistory } from './employment-history/employment-history.component';
import { MikeDao } from '../../profile-data/mikedao';
import { Portfolio } from './portfolio/portfolio';
import { SkillsSummary } from './skills-summary/skills-summary';
import { Education } from './education/education';
import { ProfileContext } from './profile.context';
import Popup from 'reactjs-popup';
import { IPortfolio } from '../../@types/profile/profile';
import { PortfolioContent } from './portfolio-content/portfolio-content';

const profile = MikeDao;

export const Profile = (props: React.Props<{}>) => {
  const [ modalPortfolio, setModalPortfolio ] = useState<IPortfolio | null | undefined>();
  return (
    <ProfileContext.Provider value={{ setModalPortfolio }}>
      <span id="profile" />
      <PersonalSummary profile={profile} />
      <span id="portfolio" />
      <Portfolio profile={profile} />
      <span id="education" />
      <Education profile={profile} />
      <span id="employment-history" />
      <EmploymentHistory profile={profile} />
      <span id="skills-availability" />
      <SkillsSummary profile={profile} />
      <Popup
        open={!!modalPortfolio} closeOnEscape={false} closeOnDocumentClick={false}
        contentStyle={{
          width: '100%',
          height: '100%',
          backgroundColor: 'transparent',
          border: 0,
          padding: 0,
        }}
      >
        <PortfolioContent profile={profile} portfolio={modalPortfolio} onClick={() => setModalPortfolio(null)} />
      </Popup>
    </ProfileContext.Provider>
  );
};
